import { EmpresaService } from './../../../service/sigobra/empresa.service';
import { Component, OnInit } from '@angular/core';
import { Empresa } from '../../../model/objetc/empresa';
import { Router } from '@angular/router';
import { Base } from '../../../model/base';
import { Camera } from '@ionic-native/Camera/ngx';
import { CameraOptions } from '@ionic-native/Camera/ngx';
import { ViacepService } from './../../../service/viacep/viacep.service';
import { Estado } from '../../../model/enums/estado.enum'; 


@Component({
  selector: 'app-empresa-inserir',
  templateUrl: './empresa-inserir.page.html',
  styleUrls: ['./empresa-inserir.page.scss'],
})
export class EmpresaInserirPage implements OnInit {

  empresa: Empresa = new Empresa();
  constructor(public base: Base, private es: EmpresaService, private camera: Camera, private router: Router,
              private vcs: ViacepService ) { }

  ngOnInit() {
    if ((this.es.empresa !== undefined) && (this.es.empresa !== null)) {
      this.empresa = this.es.empresa;
    }
  }

  salvar() {
    this.salvando();
  }

  salvando() {
    if ((this.empresa.ativo === undefined) || (this.empresa.ativo === null)) {
      this.empresa.ativo = true;
    }
    this.base.present();
    this.es.salvar(this.empresa).subscribe(
      data => {
        this.es.empresa = new Empresa();
        this.base.dismiss();
        this.router.navigate(['empresas']);
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha ao salvar empresa :' + this.base.tratarErro(error));
      }
    );
  }

  keyDownFunction(event: any) {

  }

  onFileSelected(event: any) {
    console.log(event);
    let files = event.target.files;
    let file = files[0];

    if (files && file) {
      let reader = new FileReader();

      reader.onload = this.converteToBase64Depois.bind(this);

      reader.readAsBinaryString(file);
    }
    console.log(this.empresa);
  }

  converteToBase64Depois(readerEvt) {
    const binaryString = readerEvt.target.result;
    this.empresa.logo = btoa(binaryString);
  }

  addFoto(local: number) {
    const options: CameraOptions = {
      quality: 100,
      destinationType: this.camera.DestinationType.FILE_URI,
      encodingType: this.camera.EncodingType.JPEG,
      mediaType: this.camera.MediaType.PICTURE,
      sourceType: local
    };

    this.camera.getPicture(options).then((imageData) => {
      // imageData is either a base64 encoded string or a file URI
      // If it's base64 (DATA_URL):
      this.empresa.logo = imageData;
    }, (err) => {
      // Handle error
    });
  }

  tirarFoto() {
    this.addFoto(1);
  }

  consultaCEP(cep) {
    if (cep !== null && cep !== undefined && cep !== '') {
      cep = cep.replace(/\D/g, '');
      if (cep.length > 7) {
        this.base.present();
        this.vcs.consultaCEP(cep).subscribe(
          data => {
            this.base.dismiss();
            this.popula(data);
          }, error => {
            this.base.dismiss();
            this.base.mensagemErro('Falha ao buscar CEP :' + this.base.tratarErro(error));
          }
        );
      }
    }
  }

  popula(data){   
    if (data.erro !== undefined && data.erro !== null && data.erro) {
      this.base.mensagemErro('Falha ao consultar endere√ßo do CEP');
      return;
    }
    this.empresa.logradouro = data.logradouro;
    this.empresa.complemento = data.complemento;
    this.empresa.bairro = data.bairro;
    this.empresa.cidade = data.localidade;
    this.empresa.uf = data.uf;   
    for (var uf in Estado) {
      if (data.uf === uf){
        this.empresa.estado = Estado[uf];
        break;
      }       
    }
  }


}

